import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import Alert from '../../Components/All/Alert'
import { getMenstruals } from "../../Helpers/Period";
import Calendar from 'react-awesome-calendar';

const Logbook = () => {

  const [menstruals, setMenstruals] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [events, setEvents] = useState([])
  useEffect(() => {
    getMenstruals()
      .then(data => {
        setMenstruals(data)
        let e = []
        data.dbRes.menstrualDetails.map((item, index) => {
          let data = {
            id: index,
            color: '#fd3153',
            from: new Date(item.periodStart * 1000).toISOString(),
            to: new Date(item.periodEnd * 1000).toISOString(),
            title: 'Periods'
          }
          e.push(data)
        })
        setEvents(e)
        setLoading(false)
      }).catch(err => {
        setError(err)
      })
  }, [])
  return (
    <AppBase>
      <div className="flex flex-col w-screen p-2 items-center">
        <br />
        {!error ? '' : <Alert type="error" message="An Error Occured!" />}
        {loading ? <Alert type="success" message="Loading" /> :
          <>
            <h2 className="text-2xl font-bold">
              Calendar
            </h2>
            <Calendar
              events={events}
            />
          </>
        }
      </div>
    </AppBase>
  )
}

export default Logbook