import React, { useEffect, useState } from "react"
import LoginBase from "../Components/Base/LoginBase"

import { getUser } from "../Helpers/Auth"
import { updateProfile } from "../Helpers/Profile"

import { useNavigate } from "react-router-dom"
import { create } from "../Helpers/Period"



const Details = () => {
    const navigate = useNavigate()
    const [userType, setUserType] = useState(0)

    useEffect(() => {
        if (getUser().userType !== "") {
            return navigate('/dashboard')
        }

        document.body.className = "bg-gradient-to-t from-[#E99DFF] to-[#FEFCFF] via-[#F6D7FF] bg-no-repeat flex justify-center w-screen items-center h-screen font-montserrat"

        return () => {
            document.body.className = ""
        }
    })

    const addDetails = async (e) => {
        e.preventDefault()
        try {
            const update = {
                update: {
                  firstName: e.target.firstName.value,
                  lastName: e.target.lastName.value,
                  dateOfBirth: e.target.dob.value,
                  userType: e.target.userType.value == 1 ? 'self' : 'partner',
                }
            }
            const menstrual = {
                cycleLength: e.target.cycleLength.value ? e.target.cycleLength.value : 28,
                periodLength: e.target.periodLength.value ? e.target.periodLength.value : 7,
                lastPeriod: {
                    periodStart: Math.floor(new Date(e.target.periodStart.value).getTime() / 1000),
                    periodEnd: Math.floor(new Date(e.target.periodEnd.value).getTime() / 1000)
                }

            }
            const updateCall = await updateProfile(update)
            if(updateCall.success) {
                const menstrualCall = await create(menstrual)
                if(menstrualCall.success) {
                    let data = JSON.parse(window.localStorage.getItem('user'))
                    data.userType = e.target.userType == 1 ? 'self' : 'partner'
                    window.localStorage.setItem('user', JSON.stringify(data))
                    return navigate('/dashboard')
                }else {
                    alert('error')
                }
            }else {
                alert("error")
            }

        }catch(err) {
            alert(err)
        }
    }

    return (
        <div className="w-full h-screen flex justify-center items-center">
            <form onSubmit={addDetails} className="overflow-auto h-[90%]">
                <div className="bg-white rounded-3xl shadow-lg p-8">
                    <div className="mb-4 mt-12">
                        <label for="firstName" className="block text-xl mb-3">First Name</label>
                        <input
                            type="text"
                            id="firstName"
                            placeholder="First Name"
                            className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                        />
                    </div>
                    <div className="mb-4">
                        <label for="lastName" className="block text-xl mb-3">Last Name</label>
                        <input
                            type="text"
                            id="lastName"
                            placeholder="Last Name"
                            className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                        />
                    </div>
                    <div className="mb-4">
                        <label for="usertype" className="block text-xl mb-3">User Type</label>
                        <select
                            id="userType"
                            name="userType"
                            onChange={(e) => setUserType(e.target.value)}                            
                            className="w-full h-12 pl-2 border-2 border-black rounded-full mb-5"
                        >
                            <option value={0}>Select Type</option>
                            <option value={1}>Self</option>
                            <option value={2}>Partner</option>
                        </select>
                    </div>
                    <div className="mb-4">
                        <label for="dob" className="block text-xl mb-3">Date Of Birth</label>
                        <input
                            type="date"
                            id="dob"
                            name="dob"
                            className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                        />
                    </div>
                    {userType != 1 ? '' :
                        <div className="mb-4">
                            <label for="cycleLength" className="block text-xl mb-3">Cycle Length</label>
                            <input
                                type="number"
                                id="cycleLength"
                                name="cycleLength"
                                placeholder="Cycle Length"
                                className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                            />
                        </div>
                    }
                    {userType != 1 ? '' :
                        <div className="mb-4">
                            <label for="periodLen" className="block text-xl mb-3">Period Length</label>
                            <input
                                type="number"
                                id="periodLen"
                                name="periodLength"
                                placeholder="Length"
                                className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                            />
                        </div>
                    }
                    {userType != 1 ? '' :
                        <div className="mb-4">
                            <label for="periodStart" className="block text-xl mb-3">Period Start ( Last )</label>
                            <input
                                type="date"
                                id="periodStart"
                                name="periodStart"
                                placeholder="Last Period Start Date"
                                className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                            />
                        </div>
                    }
                    {userType != 1 ? '' :
                        <div className="mb-4">
                            <label for="periodEnd" className="block text-xl mb-3">Period End ( Last )</label>
                            <input
                                type="date"
                                id="periodEnd"
                                name="periodEnd"
                                placeholder="Last Period Start Date"
                                className="w-full h-12 p-5 border-2 border-black rounded-full mb-5"
                            />
                        </div>
                    }
                    <div className="flex justify-center items-center">
                        <button
                            type="submit"
                            className="bg-[#FF7A7A] text-white w-[10rem] h-[50px] p-4 rounded-full font-bold text-2xl mb-7 flex justify-center items-center"
                        >
                            SAVE
                        </button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default Details