import React, { useEffect, useState } from "react";
import useRazorpay from 'react-razorpay'
import AppBase from "../../Components/Base/AppBase";
import { ArrowPathIcon } from "@heroicons/react/24/solid";
import Alert from "../../Components/All/Alert";
import { getCart, removeFromCart } from "../../Helpers/Cart";
import CartProduct from "../../Components/Shop/CartProduct";
import { useNavigate } from "react-router-dom";
import { getAllAddress } from "../../Helpers/Address";
import Modal from "../../Components/All/Modal";
import { getUser } from "../../Helpers/Auth";
import Loading from "../../Components/All/Loading";
import { capturePayment, newOrder } from "../../Helpers/Checkout";

const Checkout = () => {

    const [cart, setCart] = useState(false)
    const [addresses, setAddresses] = useState(false)
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    const [address, setAddress] = useState(false)
    const [showModal, setShowModal] = useState(false)
    const [success, setSuccess] = useState(false)
    const [order, setOrder] = useState(null)
    const [paymentId, setPaymentId] = useState(null)
    const navigate = useNavigate()
    // const [Razorpay] = useRazorpay()
    const [Razorpay] = useRazorpay()
    useEffect(() => {
        getCart()
            .then((data) => {
                if (data.success) {
                    setCart(data)
                    setLoading(false)
                } else {
                    setError(data.message)
                }
            })
            .catch(err => setError(err))
        getAllAddress()
            .then((data) => {
                if (data.success) {
                    setAddresses(data.dbRes)
                    setAddress(data.dbRes[0])
                    setLoading(false)
                } else {
                    setError(data.message)
                }
            })
        //     .catch(err => setError(err))
    }, [])

    const remove = () => {
        return;
    }
    const changeAddress = async (e) => {
        let index = addresses.findIndex(x => x._id === e.target.value)
        setAddress(addresses[index])

    }
    const handlePayment = async () => {
        setLoading(true)

        try {
            const orderCreate = await newOrder({ addressId: address._id })
            if(!orderCreate.dbRes.rzpOrder) {
                setError(true)
                setLoading(false)
                return
            }
            setOrder(orderCreate)
            
            const options = {
                key: "rzp_test_UygYfS9TeNaaTj",
                amount: cart.totalPrice ? cart.totalPrice * 100 : 0,
                currency: "INR",
                name: "Menstromy",
                description: order ? order._id : '',
                order_id: order ? order.rzpOrder.id : '',
                handler:  (response) => {
                    window.localStorage.setItem('payment_id', JSON.stringify(response))
                    capturePayment({
                        orderId: orderCreate ? orderCreate.dbRes._id : '',
                        paymentId: response.razorpay_payment_id,
                    })
                        .then((data) => {
                            if(data.success) {
                                setSuccess(data)
                                setLoading(false)
                                window.location.href = `/order/${data.dbRes._id}`
                            }else {
                                setError(data)
                                setLoading(false)
                            }
                           
                        })
                        .catch((error) => {
                            setLoading(false)
                            setError(error)
                        })
                },
                modal: {
                    ondismiss: function () {
                        setLoading(false)
                    }
                },
                prefill: {
                    name: address ? address.firstName + ' ' + address.lastName : '',
                    email: getUser().email,
                    contact: address ? address.mobileNumber : '',
                },
                notes: {
                    name: address ? address.firstName + ' ' + address.lastName : '',
                    email: getUser().email,
                    contact: address ? address.mobileNumber : ''
                },
                theme: {
                    color: "#FF7A7A",
                },
            }

            const rzp1 = new Razorpay(options)

            rzp1.on("payment.failed", (response) => {
                setLoading(false)
                setError(response.error)
            })

            rzp1.open()
        } catch (err) {
            setError(err.toString())
        }

    }
    return (
        <AppBase>
            {success ? <Alert type="success" message={success.message} /> : ''}
            {error ? <Alert type="warning" message={`Note the payment id: ${window.localStorage.getItem('payment_id') ? JSON.parse(window.localStorage.getItem('payment_id')).razorpay_payment_id: 'UNDEFINED'} if your money got deducted.`} /> : ''}
            {loading ? <Loading /> :
                <>
                    <div className="p-4 overflow-y-scroll h-[70%]">

                        {!loading ? '' : <ArrowPathIcon width={30} height={30} className="animate-spin" />}
                        {!error ? '' : <Alert type="error" message="An Error Occured!" />}
                        {!loading && cart.dbRes ? <CartProduct checkout={true} removeItem={remove} product={cart.dbRes.cartItems} /> : <Alert type="warning" message="No Items In Your Cart!" />}
                    </div>
                    <div className="p-3 relative">
                        <h1 className="mb-3 font-semibold text-xl">Deliver to:</h1>
                        <select
                            onChange={(e) => changeAddress(e)}
                            className="w-full mt-3 top-0 right-0 px-3 py-1 rounded-lg bg-[#FF7A7A] text-white"
                        >
                            <option value={0}>Select Address</option>

                            {!addresses ? '' :
                                addresses.map((item) => {
                                    return (
                                        <option value={item._id}>
                                            {item ? item.firstName : ''} {item ? item.lastName : ''}
                                            {item ? item.houseNumber : ''}, {item ? item.locality : ''} ({item ? item.landmark : ''}), {item ? item.street : ''}, {item ? item.city : ''}, {item ? item.state : ''} - {item ? item.pincode : ''}
                                            {item ? item.mobileNumber : ''}
                                        </option>
                                    )
                                })
                            }
                        </select>
                        <center>OR</center>
                        <Modal showModal={showModal} setShowModal={setShowModal} />
                        <button
                            onClick={() => setShowModal(true)}
                            className="w-full top-0 right-0 px-3 py-1 rounded-lg bg-[#FF7A7A] text-white"
                        >
                            Add New Address
                        </button>
                        {!address ? '' :
                            <p className="text-lg">
                                <span className="font-semibold">{address ? address.firstName : ''} {address ? address.lastName : ''}</span><br />
                                <span className="text-wrap mr-3">{address ? address.houseNumber : ''}, {address ? address.locality : ''} ({address ? address.landmark : ''}), {address ? address.street : ''}, {address ? address.city : ''}, {address ? address.state : ''} - {address ? address.pincode : ''}</span><br />
                                <span>{address ? address.mobileNumber : ''}</span><br />
                            </p>
                        }
                    </div>
                    <div
                        className="fixed bottom-0 h-20 w-full border-t-4 border-gray-300 flex justify-center items-center gap-3 text-lg font-bold bg-white"
                    >
                        <button
                            className="px-6 h-full w-1/2 text-lg"
                            onClick={() => navigate('/cart')}
                        >
                            Back To Cart
                        </button>
                        <button
                            onClick={() => handlePayment()}
                            className="px-6 h-full w-1/2 text-lg bg-[#FF7A7A] text-white"
                        >
                            Pay {cart ? cart.totalPrice + '.00' : 0.00}
                        </button>
                    </div>
                </>
            }
        </AppBase>
    )
}

export default Checkout