


const Day = (props) => {

    return(
        <div 
            className={`text-[50px] flex flex-col items-center justify-center rounded-full w-[250px] h-[250px] border-black border-2`}
        >
            <span className="text-2xl text-wrap">{props.text}</span>
            <strong>{props.day} Days</strong>
        </div>
    )
}

export default Day