import React, { useEffect, useState } from "react"
import AppBase from "../../Components/Base/AppBase"
import Alert from '../../Components/All/Alert'
import { getUser } from '../../Helpers/Auth/index'
import { getProfile, updateProfile } from "../../Helpers/Profile"

import { ArrowPathIcon } from "@heroicons/react/24/solid"

const Profile = () => {

  const [user, setUser] = useState(false)
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)

  useEffect(() => {
    window.document.title = "Profile | Menstromy"
    getProfile()
      .then((data) => {
        setUser(data.dbRes)
      })
      .catch(err => setError(true))
  }, [])

  const formSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    try {
      let update = {
        update: {
          firstName: e.target.firstName.value,
          lastName: e.target.lastName.value,
          username: e.target.username.value,
        }
      }
      update = JSON.stringify(update)
      const updateRequest = await updateProfile(update)
      if(updateRequest.success) {
        setUser(updateRequest.dbRes)
        window.localStorage.setItem('user', JSON.stringify(updateRequest.dbRes))
        setSuccess(true)
        setLoading(false)
      }else {
        setError(error)
        setLoading(false)
      }
    }catch(err) {
      setError(err)
      setLoading(false)
    }
   
  }

  return (
    <AppBase>
      <div className="flex flex-col items-center justify-center">
        <div className="mb-2 mt-4 rounded-full w-[150px] h-[150px] bg-[#FF7A7A] flex flex-col items-center justify-center font-bold text-white text-6xl uppercase">
          {getUser() ? getUser().firstName[0] + getUser().firstName[1] : 'US'}
        </div>
      {error ? <Alert type="error" message={error.toString()} /> : '' }
      {success ? <Alert type="success" message={`Updated!`} /> : '' }
      {!user ? <ArrowPathIcon width={30} height={30} className="animate-spin" />:
          <form onSubmit={formSubmit} className="mt-2 flex flex-col">
            <div className="mt-2 flex flex-col">
              <label>First Name</label>
              <input
                name="firstName"
                type="text"
                onChange={(e) => setUser({ ...user, firstName: e.target.value })}
                className="w-[300px] h-[40px] rounded-md p-2 text-xl text-center"
                value={user.firstName ? user.firstName : ''}
              />
            </div>
            <div className="mt-2 flex flex-col">
              <label>Last Name</label>
              <input
                name="lastName"
                type="text"
                onChange={(e) => setUser({ ...user, lastName: e.target.value })}
                className="w-[300px] h-[40px] rounded-md p-2 text-xl text-center"
                value={user.lastName ? user.lastName : ''}
              />
            </div>
            <div className="mt-2 flex flex-col">
              <label>Date Of Birth</label>
              <input
                name="dateOfBirth"
                disabled
                type="date"
                className="w-[300px] h-[40px] rounded-md p-2 text-xl text-center"
                value={user.dateOfBirth ? user.dateOfBirth: 'Not Available'}
              />
            </div>
            <div className="mt-2 flex flex-col">
              <label>Email</label>
              <input
                name="email"
                disabled
                className="w-[300px] h-[40px] rounded-md p-2 text-xl text-center"
                value={user.email ? user.email : 'Not Available'}
              />
            </div>
            <div className="mt-2 flex flex-col">
              <label>Username</label>
              <input
                name="username"
                onChange={(e) => setUser({ ...user, username: e.target.value })}
                className="w-[300px] h-[40px] rounded-md p-2 text-xl text-center"
                value={user.username ? user.username : ''}
              />
            </div>
            <div className="mt-4 flex flex-col">
              <button
                type="submit"
                onClick={updateProfile}
                disabled={loading ? true : false}
                className="w-[300px] h-[40px] flex items-center justify-center bg-[#FF7A7A] text-white rounded-md p-2 text-xl text-center"
              >
                { loading ? <span className="flex flex-row">Updating <ArrowPathIcon width={30} height={30} className="animate-spin" /></span> : "Update"}
              </button>
            </div>
          </form>
        }
      </div>
    </AppBase>
  )
}

export default Profile