import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import { ArrowLeftCircleIcon, ArrowPathIcon, ArrowRightCircleIcon } from "@heroicons/react/24/solid";
import Alert from "../../Components/All/Alert";
import { useNavigate, useParams } from "react-router-dom";
import { getAllOrders, getOrder } from "../../Helpers/Checkout";
import CartProduct from "../../Components/Shop/CartProduct";
import OrdersCard from "../../Components/Shop/OrdersCard";


const Orders = () => {

    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    const [orders, setOrders] = useState(null)
    const navigate = useNavigate()
    useEffect(() => {
        getAllOrders(1, 'ok')
            .then((data) => {
                if (data.success) {
                    setOrders(data)
                    setLoading(false)
                } else {
                    setError(data.message)
                }
            })
            .catch(err => setError(err))

    }, [])

    const changePage = async (page) => {
        setLoading(true)
        try {
            const moreOrders = await getAllOrders(page, 'ok')
            if(moreOrders.success) {
                setOrders(moreOrders)
                setLoading(false)
            }else {
                setError(moreOrders)
                setLoading(false)
            }
        }catch(err) {
            setError(err)
        }
    }

    return (
        <AppBase>
            <h2 className="text-2xl font-bold">
                All Orders
            </h2>
            {!loading ? '' : <ArrowPathIcon width={30} height={30} className="animate-spin" /> }
            { orders ? <OrdersCard orders={orders.orders.docs}/> : ''}
            <div className="flex flex-row justify-between w-screen p-4">
                <button disabled={orders && orders.orders.prevPage ? false : true} onClick={() => changePage(orders.orders.prevPage)} className="flex flex-row p-2">
                    <ArrowLeftCircleIcon width={30} height={30} /> Previous
                </button>
                <button disabled={orders && orders.orders.nextPage ? false : true} onClick={() => changePage(orders.orders.nextPage)} className="flex flex-row p-2">
                    <ArrowRightCircleIcon width={30} height={30} /> Next
                </button>
            </div>
        </AppBase>  
    )
}

export default Orders