import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import Alert from "../All/Alert"
import { addToCart } from "../../Helpers/Cart"

const Product = (props) => {
    const navigate = useNavigate()
    const [error, setError] = useState([])
    const [success, setSuccess] = useState([])
    useEffect(() => {
        props.products.map((item, index) => setError([...error, { index: false }]))
        props.products.map((item, index) => setSuccess([...success, { index: false }]))

    }, [])
    const addtoCart = async (e) => {
        e.preventDefault()
        try {
            const cartData = {
                productId: e.target.productId.value,
                quantity: e.target.qty.value
            }

            const add = await addToCart(cartData)
            if(add.success) {
                setSuccess([...success, success[parseInt(e.target.index.value)].index = true])
            }else {
                setError([...error, success[parseInt(e.target.index.value)].index = true])
                
            }
        }catch(err) {
            setError([...error, success[parseInt(e.target.index.value)].index = true])
        }
    }
    
    return (
        props.products.map((item, index) => {
            return (
                <div key={item._id} className="flex flex-row shadow border-gray-100 border rounded-xl h-auto w-[350px] mb-2">
                    <div className="w-1/3" onClick={() => navigate(`/product/${item._id}`)}>
                        <img
                            src={item.productImages.length > 0 ? item.productImages[0] : 'Not Available'}
                            className='w-full h-full'
                        />
                    </div>
                    <div className="w-2/3 flex flex-col p-2">
                        <h2 className="text-lg font-semibold mb-1" onClick={() => navigate(`/product/${item._id}`)}>{item.productName}</h2>
                        <p className="text-xl font-medium mb-1">{item.productPrice}</p>
                        {item.availableUnits === 0 ? <div className="w-full h-1/3 flex flex-row p-2 items-center justify-center text-lg font-semibold"> Out Of Stock </div> :
                            <div className="w-full h-1/3 flex flex-col p-2">
                                {error.length == 0 ? '' :
                                    error[index].index ? <Alert type="error" message={'Something went wrong!'} /> : '' 
                                }
                                <form className="w-full h-full flex flex-row" onSubmit={addtoCart}>
                                    <input type="hidden" name="productId" value={item._id} />
                                    <input type="hidden" name="index" value={index} />
                                    <select name="qty" className="w-1/2 h-[40px]">
                                        <option value={1}>Qty: 1</option>
                                        <option value={2}>Qty: 2</option>
                                        <option value={3}>Qty: 3</option>
                                        <option value={4}>Qty: 4</option>
                                    </select>
                                    <button type="submit" className="w-full mr-1 rounded h-[40px] font-semibold text-white bg-[#FF7A7A] mb-0">
                                    {success.length == 0 ? '' :
                                        success[index].index ? 'Added!' : 'Add To Cart' 
                                    }
                                    </button>
                                </form>
                            </div>
                        }
                    </div>
                </div>
            )
        })
    )
}

export default Product