import React, { useEffect, useState } from "react"
import { getUser } from "../../Helpers/Auth"
import { useLocation, useNavigate } from "react-router-dom"
import { 
    ArrowRightCircleIcon,
    BellIcon,
    UserCircleIcon,
    Cog6ToothIcon,
    ArrowLeftEndOnRectangleIcon,
    ArrowLeftCircleIcon,
    ShoppingBagIcon,
    ArchiveBoxIcon
} from '@heroicons/react/24/solid'
import BottomBar from "./BottomBar"
import Drawer from "./Drawer"

const AppBase = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const [isOpen, setIsOpen] = useState(false)

    useEffect(() => {
        // if(getUser().userType === "") {
        //     return navigate('/details')
        // }
    })
    const drawerItems = [
        {
            title: 'Profile',
            icon: <UserCircleIcon width={30} height={30} className={'text-black'} />,
            href: '/profile'
        },
        {
            title: 'Your Orders',
            icon: <ArchiveBoxIcon width={30} height={30} className={'text-black'} />,
            href: '/orders'
        },
        {
            title: 'About',
            icon: <ArrowRightCircleIcon width={30} height={30} className={'text-black'} />,
            href: '/about'
        },
        {
            title: 'Privacy Policy',
            icon: <ArrowRightCircleIcon width={30} height={30} className={'text-black'} />,
            href: '/privacy-policy'
        },
        {
            title: 'Terms & Conditions',
            icon: <ArrowRightCircleIcon width={30} height={30} className={'text-black'} />,
            href: '/terms-and-conditions'
        },
        {
            title: 'Settings',
            icon: <Cog6ToothIcon width={30} height={30} className={'text-black'} />,
            href: '/settings'
        },
        {
            title: 'Logout',
            icon: <ArrowLeftEndOnRectangleIcon width={30} height={30} className={'text-black'} />,
            href: '#logout'
        },
    ]
    return (
        <div className="h-screen w-screen md:overflow-hidden">
            {!location.pathname.includes('product') ? '' :
                <div className="flex flex-row justify-between h-[10%] p-4 text-[#FF7A7A]">
                    <span className="material-symbols-outlined text-3xl" onClick={() => navigate('/shop')}> <ArrowLeftCircleIcon width={40} height={40} /> </span>
                    <span className="material-symbols-outlined text-3xl" onClick={() => navigate('/cart')}> <ShoppingBagIcon width={40} height={40} /> </span>
                </div>
            }

            {location.pathname.includes('product') ? '' :
            <div className="flex flex-row justify-between h-[10%] p-4 text-[#FF7A7A]">
                <div 
                    className="mt-2 flex flex-row text-black"
                    onClick={() => isOpen ? setIsOpen(false): setIsOpen(true)}
                >
                    <UserCircleIcon 
                        width={40} 
                        height={40} 
                        className={'text-black-400'} 
                        />
                    <p className="mt-2 font-sans font-semibold">
                        { getUser() ? getUser().firstName.split(" ")[0] : 'User'}
                    </p>
                </div>
                <BellIcon 
                    width={25} 
                    height={25} 
                    className="mt-4" 
                    />
               
            </div>
            }
            <div className={`flex flex-col items-center drop-shadow-2xl h-[90%]  ${props.style}`}>
                <div className={`overflow-y-scroll ${location.pathname.includes('product') ? "h-[100%]" : "h-[90%]"} flex flex-col items-center`}>
                    {props.children}
                </div>
                {location.pathname.includes('/product') ? '' : <BottomBar /> }
            </div>
            <Drawer isOpen={isOpen} setIsOpen={setIsOpen}>
                <div className="flex flex-col items-center justify-center font-semibold">
                    <div className="mb-2 rounded-full w-[100px] shadow-lg h-[100px] bg-[#FF7A7A] flex flex-col items-center justify-center font-bold text-white text-2xl uppercase">
                        { getUser() ? getUser().firstName[0] + getUser().firstName[1] : 'US'}

                    </div>
                    <p>{getUser() ? getUser().firstName + " " + getUser().lastName : 'FirstName LastName'}</p>
                    <p className="text-sm">{ getUser() ? "@" + getUser().username : '@username'}</p>

                </div>
                <div className="flex flex-col items-center justify-center mt-4">
                    <ul>
                        {drawerItems.map((item) => {
                            return(
                                <li onClick={() => navigate(item.href)} className="w-[250px] h-[50px] shadow flex items-center flex-row p-4 hover:shadow-lg cursor-pointer">
                                    {item.icon}
                                    <span className="w-[10px]"></span>
                                    {item.title}
                                </li>
                            )
                        })}
                    </ul>
                </div>
            </Drawer>
        </div>


    )
}

export default AppBase