import React from "react"
import { 
    HomeIcon, 
    ShoppingCartIcon, 
    ShoppingBagIcon,
    CalendarIcon,
} from '@heroicons/react/24/solid'
import { useLocation, useNavigate } from "react-router-dom"

const BottomBar = (props) => {

    const location = useLocation()
    const navigate = useNavigate()

    const menuItems = [
        {
            title: 'Home',
            icon: <HomeIcon width={30} height={30} className={location.pathname === '/dashboard' ? 'text-black' : 'text-[#0000005f] '} />,
            active: location.pathname === '/dashboard' ? true : false,
            href: '/dashboard'
        },
        {
            title: 'Calendar',
            icon: <CalendarIcon width={30} height={30} className={location.pathname === '/calendar' ? 'text-black' : 'text-[#0000005f]'} />,
            active: location.pathname === '/calendar' ? true : false,
            href: '/calendar'
        },
        {
            title: 'Shop',
            icon: <ShoppingBagIcon width={30} height={30} className={location.pathname === '/shop' ? 'text-black' : 'text-[#0000005f]'} />,
            active: location.pathname === '/shop' ? true : false,
            href: '/shop'
        },
        {
            title: 'Cart',
            icon: <ShoppingCartIcon width={30} height={30} className={location.pathname === '/cart' ? 'text-black' : 'text-[#0000005f]'} />,
            active: location.pathname === '/cart' ? true : false,
            href: '/cart'
        },
    ]

    return(
        <div className="w-screen h-[10%] flex flex-row justify-center gap-12 border-t-2 border-gray-200 shadow">
            { menuItems.map((item) => {
                return(
                    <div 
                        key={item.href}
                        onClick={() => navigate(item.href)} 
                        className={`flex flex-col pt-2 items-center ${item.active ? 'border-t-4 border-[#e6106d]' : ''} rounded w-[40px]`}
                    >
                        {item.icon}
                        <p className={item.active ? 'text-black font-semibold' : 'text-[#0000005f]'}>
                            {item.title}
                        </p>      
                    </div>
                )
            })}
        </div>
    )
}

export default BottomBar