import { useNavigate } from "react-router-dom"

const OrdersCard = (props) => {
   
    const navigate = useNavigate()
    return(
       
        props.orders.map((item) => {
            return(
                <div onClick={() => navigate(`/order/${item._id}`)} key={item._id} className="flex flex-row shadow border-gray-100 border rounded-xl h-auto w-[350px] mb-2">
                    <div className="w-full flex flex-col p-2">
                        <h2 className="text-lg font-semibold mb-1">Order#{item._id}</h2>
                        <p className="text-sm font-semibold">
                            Order Status: {item.order_status}
                        </p>
                        <p className="text-sm font-semibold">
                            Payment Status: {item.payment_status}
                        </p>
                        <p className="text-sm font-bold">
                            Total Amount: {item.rzpOrder ? item.rzpOrder.amount / 100 + '.00' : ''}
                        </p>

                    </div>
                </div>
            )
        })
    )
}

export default OrdersCard