import "../../Assets/Style/solar.css"

const Loading = (props) => {
    return(
    
        <div className='bg-[#212738] fixed w-screen h-screen top-0 left-0 z-50 flex items-center justify-center transition-all'>
           <div class="portfolio-loader">
                <div class="sun"></div>
                <div class="orbit orbit1"><div class="planetX planet1"></div></div>
                <div class="orbit orbit2"><div class="planetX planet2"></div></div>
                <div class="orbit orbit3"><div class="planetX planet3"></div></div>
            </div>
            { props.text ? <h1 className="text-white font-[30px] mt-[300px]">{props.text}</h1> : null}
        </div>
    )
}

export default Loading