import React, { useEffect } from "react";
import ImageBG from '../../Assets/Media/bg-login-register.jpeg';
import { getUser, getToken } from "../../Helpers/Auth";
import { useNavigate } from "react-router-dom";

const LoginBase = (props) => {
    const navigate = useNavigate()
    useEffect(() => {
        if(getUser() && getToken()) {
            return navigate('/dashboard')
        }
    })
  return (
    <div>
        <div>
            <img
                src={ImageBG}
                class="rounded-br-[100rem] rounded-bl-[100rem] w-[900px] h-[230px]"
            />
        </div>
        <div class="flex justify-center items-center flex-col">
            <h1 class="text-center font-semibold text-4xl mb-10 mt-10">{props.title}</h1>
            <div class="items-center text-gray-500 my-3 flex mb-7">
                <hr class="border border-gray-600 my-1 w-16" />
                <p class="text-center text-lg col-span-1 mx-3">Continue With</p>
                <hr class="border border-gray-600 my-1 w-16" />
            </div>
            <div>
                {props.children}
            </div>
        </div>
    </div>
  )
}

export default LoginBase