import React, { useEffect, useState } from "react"
import { Link, redirect, useNavigate } from "react-router-dom"

import { signIn } from "../Helpers/Auth"
import { auth, signInWithGooglePopup } from "../firebase"

import LoginBase from "../Components/Base/LoginBase"
import Loading from "../Components/All/Loading"
import Alert from "../Components/All/Alert"

import GoogleLogo from '../Assets/Media/google-logo.png'

const Login = () => {
  const navigate = useNavigate()
  const [error, setError] = useState(false)
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)

  useEffect(() => {
    if(window.localStorage.getItem('user') && JSON.parse(window.localStorage.getItem('user')).token) {
        return window.location.href = "/dashboard"
    } 
  }, [redirect])

  const signInWithGoogle = async () => {

    setLoading(true)

    try{

      let login = await signInWithGooglePopup()
      let token = await auth.currentUser.getIdToken()
      let loginWithServer = await signIn({ firebaseToken: token })
      if(loginWithServer.error) {
        setError("Something went wrong! \n If You are not registered. Please use signup option!")
        setLoading(false)
      }else {
        setSuccess(loginWithServer)
        window.localStorage.setItem('user', JSON.stringify(loginWithServer.dbRes))
        window.localStorage.setItem('token', loginWithServer.token)

        setLoading(false)
        if(loginWithServer.userType) {
          return navigate('/dashboard')
        }else {
          return navigate('/details')
        }
      }
    }catch(err) { 

      let error = err.toString()

      if(error.includes("auth/popup-closed-by-user")) {

        setError("You closed the login popup.")
        setLoading(false)

      }else if (error.includes("fetch")) {

        setError("Cannot Reach Our Servers!")
        setLoading(false)

      }else {
        setError(err)
        setLoading(false)
      }
    }
  }

  return (
    <LoginBase title="Welcome back!" description="Let's see your MENSTROMY by signing in.">

    { loading ? <Loading text="Signing you in..." /> :

        <div className="flex justify-center items-center">
          <div className="text-center">
            <button
              onClick={() => signInWithGoogle()}
              className="flex items-center justify-center border-[3px] border-black w-[18rem] h-[60px] p-4 rounded-full font-semibold text-lg mb-5"
            >
              <img className="h-7 w-7 mr-5" src={GoogleLogo} alt="logo" />
                <span>Continue with Google</span>
            </button>
            <div>
              <h4 className="text-[#827878] font-medium text-lg">
                Don't have an account?
              </h4>
              <Link to={'/signup'} className="text-[#FF7A7A] text-center font-bold text-xl">
                Sign Up
              </Link>
            </div>
          </div>
        </div>

    }
    <br/>
    
    { !success ? null :
        <Alert 
          message={`You have successfully logged in as ${success.dbRes.username}. You will be redirected to your account in few seconds if not <Link to='/dashboard'>click here</Link>`}
          type="success"
        />
    }
    { !error ? null :
        <Alert 
          message={error.toString()}
          type="error"
        />
    }
    </LoginBase>
  )
}

export default Login