import React, { useState } from "react"
import { Link, useNavigate } from "react-router-dom"

import { signUp } from "../Helpers/Auth"
import { auth, signInWithGooglePopup } from "../firebase"

import LoginBase from "../Components/Base/LoginBase"
import Loading from "../Components/All/Loading"
import Alert from "../Components/All/Alert"

import GoogleLogo from '../Assets/Media/google.svg'

const Signup = () => {

  const navigate = useNavigate()
  const [error, setError] = useState(false)
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)

  const signUpWithGoogle = async () => {

    setLoading(true)

    try{

      let login = await signInWithGooglePopup()
      let token = await auth.currentUser.getIdToken()
      let registerWithServer = await signUp({ firebaseToken: token })
      if(registerWithServer.userType) {
        return navigate('/dashboard')
      }
      window.localStorage.setItem('user', JSON.stringify(registerWithServer.dbRes))
      window.localStorage.setItem('token', registerWithServer.token)
      navigate('/details')
      setLoading(false)

    }catch(err) {
      let error = err.toString()

      if(error.includes("auth/popup-closed-by-user")) {

        setError("You closed the popup.")
        setLoading(false)

      }else {

        setError(error)
        setLoading(false)

      }
    }
  }

  return (
    <LoginBase title="SignUp" description="">

    { loading ? <Loading text="Signing you up..." /> :

      <div>
        { success ? '' :
          <div className="text-center">
            <button
              onClick={() => signUpWithGoogle()}
              className="flex items-center justify-center border-[3px] border-black w-[18rem] h-[60px] p-4 rounded-full font-semibold text-lg mb-5"
            >
              <img className="h-7 w-7 mr-5" src={GoogleLogo} alt="logo" />
                <span>Continue with Google</span>
            </button>
            <div>
              <h4 className="text-[#827878] font-medium text-lg">
                Already have an account?
              </h4>
              <Link to={'/signin'} className="text-[#FF7A7A] text-center font-bold text-xl">
                Sign In
              </Link>
            </div>
          </div>
        }
        <br/>
        { !error ? null :
          <Alert
            message={error.toString()}
            type="error"
          />
        }
      </div>

    }
    </LoginBase>
  )
}

export default Signup