import config from '../config.json'
import { getToken } from '../Auth/index'

export const getAllOrders = async (page, type) => {
    let res = await fetch(`${config.host}/${config.order}/orders?page=${page}&limit=5&type=${type}`,  {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${getToken()}`
        },
        credentials: 'include',
        method: "GET"
    })
    
    let json = await res.json()

    return json

}

export const getOrder = async (orderId) => {
    let res = await fetch(`${config.host}/${config.order}/get/${orderId}`,  {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${getToken()}`
        },
        credentials: 'include',
        method: "GET"
    })
    
    let json = await res.json()

    return json

}

export const newOrder = async (body) => {
    let res = await fetch(`${config.host}/${config.order}create`,  {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${getToken()}`
        },
        credentials: 'include',
        method: "POST",
        body: JSON.stringify(body)
    })
    
    let json = await res.json()

    return json

}
export const capturePayment = async (body) => {
    let res = await fetch(`${config.host}/${config.order}payment-check`,  {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${getToken()}`
        },
        credentials: 'include',
        method: "POST",
        body: JSON.stringify(body)
    })
    
    let json = await res.json()

    return json

}