import React, { useEffect, useState } from 'react'
import AppBase from '../../Components/Base/AppBase'
import Alert from '../../Components/All/Alert'
import { useNavigate, useParams } from 'react-router-dom'
import { getProductById } from '../../Helpers/Shop'


const Product = () => {

    const { productId } = useParams()
    const [data, setData] = useState(null)
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    const navigate = useNavigate()
    useEffect(() => {
        getProductById(productId)
            .then((data) => {
                setData(data)
                setLoading(true)
            })
            .catch(err => setError(err))
    }, [productId])

    
    return (
        <AppBase>
            {error ? <Alert type="error" message="An Error Occurred!" />: ''}
            {!data ? 'Loading' :
                <div className='overflow-auto bg-white'>
                    <div className="overflow-y-auto pb-20">
                        <img
                            src={data.product.productImages.length > 0 ? data.product.productImages[0] : "https://www.tjc.co.uk/dw/image/v2/AAXN_PRD/on/demandware.static/-/Sites-master-catalog-tjc/default/dwa6398bf0/Large/68/5/6851987_2.jpg?sw=900&sh=900&sm=fit"}
                            className="w-[26rem] h-[22rem] object-cover"
                        />
                         {/* <SimpleImageSlider
                            width={'26rem'}
                            height={'22rem'}
                            images={data.product.productImages}
                            showBullets={true}
                            showNavs={true}
                        /> */}
                        <h1 className="font-semibold text-2xl ml-4 mt-4 m-r-4">
                            { data ? data.product.productName: 'Not Available'}
                        </h1>
                        <h3 className="text-gray-500 ml-4 mt-1">
                            { data ? data.product.productDescription: 'Not Available'}
                        </h3>
                        <h2 className="font-bold text-3xl ml-4 mt-3">{ data ? data.product.productPrice : 'Not Available'}</h2>
                        <hr className="border-0 bg-gray-200 my-3" style={{height: '8px'}}/>
                        {/* <div className="p-3">
                            <h1 className="font-medium text-xl mb-3">Select Size</h1>
                            <button
                                className="rounded-3xl border border-black bg-[#FF7A7A] text-white font-semibold text-xl px-4 py-1"
                            >
                                Free size
                            </button>
                        </div> */}
                        
                        {/* <hr className="border-0 bg-gray-200 my-3" style={{height: '8px'}} /> */}
                        <div className="mt-4 p-3">
                            <h3 className="font-semibold text-xl">Product Details</h3>
                            <p className="text-gray-600 mt-2">
                                { data ? data.product.productDescription: 'Not Available'}

                                {/* <span className="font-semibold">Brand:</span> Hampa<br />
                                <span className="font-semibold">Item Form:</span> Oil<br />
                                <span className="font-semibold">Number of Items:</span> 1<br />
                                <span className="font-semibold">Net Quantity:</span> 50.0 millilitre */}
                            </p>
                        </div>
                        <div
                            className="fixed bottom-0 h-20 w-full border-t-4 border-gray-300 flex justify-center items-center gap-3 text-lg font-bold bg-white"
                        >
                            <button 
                                className="px-6 h-full w-1/2"
                            >Add to Cart</button>
                            <button 
                                className="px-6 h-full w-1/2 bg-[#FF7A7A] text-white"
                            >
                                Buy Now
                            </button>
                        </div>
                    </div>
                </div>
            }
        </AppBase>
    )
}

export default Product