import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import Day from "../../Components/Dashboard/Day";
import Alert from '../../Components/All/Alert'
import { endPeriod, getMenstruals, getNextPeriodDate, startPeriod } from "../../Helpers/Period";

const Dashboard = () => {
  const [nextPeriod, setNextPeriod] = useState(null)
  const [day, setDay] = useState(0)
  const [menstruals, setMenstruals] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    getMenstruals()
      .then(data => {
        setMenstruals(data)
        if (data.dbRes.menstrualDetails[data.dbRes.menstrualDetails.length - 1].periodEnd == 0) {
          let date1 = new Date()
          let date2 = new Date(data.dbRes.menstrualDetails[data.dbRes.menstrualDetails.length - 1].periodStart * 1000)
          let dfint = date2.getTime() - date1.getTime()
          let dfind = Math.round(dfint / (1000 * 3600 * 24))
          setDay(dfind)
          setLoading(false)
        } else {
          getNextPeriodDate()
            .then(data => {
              setNextPeriod(data)
              let date1 = new Date()
              let date2 = new Date(data.value * 1000)
              let dfint = date2.getTime() - date1.getTime()
              let dfind = Math.round(dfint / (1000 * 3600 * 24))
              setDay(dfind)
              setLoading(false)
            }).catch(err => {
              setError(err)
            })
        }
      }).catch(err => {
        setError(err)
      })
  }, [])

  const start = async () => {
    setLoading(true)
    try {
      const data = {
        periodStart: Math.round(+new Date() / 1000)
      }
      const startCall = await startPeriod(data)
      if (startCall.success) {
        let date1 = new Date()
        let date2 = new Date(startCall.dbRes.menstrualDetails[startCall.dbRes.menstrualDetails.length - 1].periodStart * 1000)
        let dfint = date2.getTime() - date1.getTime()
        let dfind = Math.round(dfint / (1000 * 3600 * 24))
        setDay(dfind)
        setMenstruals(startCall)
        setLoading(false)
        
      } else {
        setError(true)
        setLoading(false)
      }
    } catch (err) {
      setError(err)
    }
  }

  const end = async () => {
    setLoading(true)
    try {
      const data = {
        periodEnd: Math.round(+new Date() / 1000),
        periodId: menstruals ? menstruals.dbRes.menstrualDetails[menstruals.dbRes.menstrualDetails.length - 1]._id : ''
      }
      const endCall = await endPeriod(data)
      if (endCall.success) {
        const nextPeriod = await getNextPeriodDate()
        let date1 = new Date()
        let date2 = new Date(nextPeriod.value * 1000)
        let dfint = date2.getTime() - date1.getTime()
        let dfind = Math.round(dfint / (1000 * 3600 * 24))
        setDay(dfind)
        setMenstruals(endCall)
        setLoading(false)
      } else {
        setError(true)
        setLoading(false)
      }
    } catch (err) {
      setError(err)
    }
  }
  return (
    <AppBase>
      <div className="flex flex-col items-center">
        <br />
        {!error ? '' : <Alert type="error" message="An Error Occured!" />}
        {loading ? <Alert type="success" message="Loading" /> :
          <>
            {
              !menstruals ? ''
                : (
                  menstruals.dbRes.menstrualDetails[menstruals.dbRes.menstrualDetails.length - 1].periodEnd != 0 ? <Day text="Next Periods In" day={day} />
                    : <Day text="Periods Since" day={day} />
                )
            }
            {
              !menstruals ? ''
                : (
                  !menstruals.dbRes.menstrualDetails[menstruals.dbRes.menstrualDetails.length - 1].periodEnd != 0 ? ''
                    :
                    <button
                      onClick={() => start()}
                      className="flex items-center justify-center shadow bg-[#FF8686] mt-4 text-white w-[16rem] h-[40px] p-4 rounded-full font-semibold text-lg mb-5"
                    >
                      <span>Periods Started ?</span>
                    </button>
                )
            }
            {
              !menstruals ? ''
                : (
                  menstruals.dbRes.menstrualDetails[menstruals.dbRes.menstrualDetails.length - 1].periodEnd != 0 ? ''
                    :
                    <button
                      onClick={() => end()}
                      className="flex items-center justify-center shadow bg-[#FF8686] mt-4 text-white w-[16rem] h-[40px] p-4 rounded-full font-semibold text-lg mb-5"
                    >
                      <span>Periods Ended ?</span>
                    </button>
                )
            }
          </>
        }
      </div>
    </AppBase>
  )
}

export default Dashboard