import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import { ArrowPathIcon } from "@heroicons/react/24/solid";
import Alert from "../../Components/All/Alert";
import { getCart, removeFromCart } from "../../Helpers/Cart";
import CartProduct from "../../Components/Shop/CartProduct";
import { useNavigate } from "react-router-dom";

const Cart = () => {

    const [cart, setCart] = useState(null)
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)

    const navigate = useNavigate()

    useEffect(() => {
        
        getCart()
            .then((data) =>  {
                if(data.success) {
                    setCart(data)
                    setLoading(false)
                }else {
                    setError(data.message)
                }
            })
            .catch(err => setError(err))
    }, [])

    const remove = async (cartItem) => {
        setLoading(true)
        try {
            const removeCart = await removeFromCart({ cartItemId : cartItem })
            if(removeCart.success) {
                navigate(0)
            }else {
                setError(removeCart.message)
            }
        }catch(err) {
            setError(err)
        }
    }

    return (
        <AppBase>
            <div className="p-4 overflow-y-scroll h-[80%]">
            
                {!loading ? '' : <ArrowPathIcon width={30} height={30} className="animate-spin" /> }
                {!error ? '' : <Alert type="error" message="An Error Occured!" /> }
                {loading ? '' :
                    cart.dbRes.cartItems.length > 0 ? <CartProduct removeItem={remove}  product={cart.dbRes.cartItems} /> : <Alert type="warning" message="No Items In Your Cart!" />
                }
            </div>
            <div className="w-screen flex flex-col items-start p-4">
                <h2 className="text-xl font-bold">
                    Total: {cart ? cart.totalPrice + '.00' : 0.00}
                </h2>
            </div>
            <button onClick={() => navigate('/checkout')} className="mt-4 w-[300px] rounded h-[40px] font-semibold text-white bg-[#6210e6bb] mb-0">
                Checkout
            </button>
        </AppBase>
    )
}

export default Cart