import React, { useState } from "react"
import { newAddress } from "../../Helpers/Address"
import { useNavigate } from "react-router-dom"

const Modal = (props) => {
    const navigate = useNavigate()
    const add = async (e) => {
        e.preventDefault()
        console.log(e.target.firstName.value)
        try {
            
            const data = {
                firstName: e.target.firstName.value,
                lastName: e.target.lastName.value,
                type: e.target.type.value,
                mobileNumber: e.target.mobileNumber.value,
                houseNumber: e.target.houseNumber.value,
                locality: e.target.locality.value,
                city: e.target.city.value,
                state: e.target.state.value,
                pincode: e.target.pincode.value,
                landmark: e.target.landmark.value,
                street: e.target.street.value,
            }
            if(data.type === '0')
                return alert("Please select address type")
           
            const addAddress = await newAddress(data)
            if(addAddress.success) {
                alert("Added New Address")
                navigate('/checkout')
            }else {
                alert(newAddress.messaage)
            }
        }catch(err) {
            console.log(err)
        }
    }
    return (
        <>
            {props.showModal ? (
                <>
                    <div className="flex justify-center items-center overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none">
                        <div className="relative w-auto my-6 mx-auto max-w-3xl">
                            <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                                <div className="flex items-start justify-between p-5 border-b border-solid border-gray-300 rounded-t ">
                                    <h3 className="text-3xl font=semibold">New Address</h3>
                                    <button
                                        className="bg-transparent border-0 text-black float-right"
                                        onClick={() => props.setShowModal(false)}
                                    >
                                        <span className="text-[#ffffff] opacity-7 h-6 w-6 text-xl block bg-gray-400 py-0 rounded-full">
                                            x
                                        </span>
                                    </button>
                                </div>
                                <div className="relative p-6 flex-auto">
                                    <form onSubmit={add} className="shadow-md rounded px-8 pt-6 pb-8 w-full h-[500px]">
                                        <div className="overflow-auto h-full mb-2">
                                            <label className="block text-black text-sm font-bold mb-1">
                                                First Name
                                            </label>
                                            <input required name="firstName" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Last Name
                                            </label>
                                            <input required name="lastName" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Mobile Number
                                            </label>
                                            <input required name="mobileNumber" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                House Number/Flat Number
                                            </label>
                                            <select required name="type" className="shadow appearance-none border rounded w-full py-2 px-1 text-black">
                                                <option value={0}>Select Address Type</option>
                                                <option value={'home'}>Home</option>
                                                <option value={'office'}>Office</option>
                                            </select>
                                            <label className="block text-black text-sm font-bold mb-1">
                                                House Number/Flat Number
                                            </label>
                                            <input required name="houseNumber" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Locality
                                            </label>
                                            <input required name="locality" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Street
                                            </label>
                                            <input required name="street" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Landmark
                                            </label>
                                            <input name="landmark" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                Pincode
                                            </label>
                                            <input required name="pincode" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                City
                                            </label>
                                            <input required name="city" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                            <label className="block text-black text-sm font-bold mb-1">
                                                State
                                            </label>
                                            <input required name="state" className="shadow appearance-none border rounded w-full py-2 px-1 text-black" />
                                        </div>
                                        <button
                                            type="submit"
                                            className="px-6 rounded h-[40px] w-1/2 text-lg bg-[#FF7A7A] text-white"
                                        >
                                            Add
                                        </button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            ) : null}
        </>
    )
}

export default Modal