import SuccessSVG from '../../Assets/Media/success.svg'
import ErrorSVG from '../../Assets/Media/error.svg'
import WarningSVG from '../../Assets/Media/warning.svg'


const Alert = (props) => {

    return(
        <div 
            className={`flex items-center rounded-full ${props.type === 'error' ? 'bg-red-500' : ''} ${props.type === 'success' ? 'bg-green-500' : ''} ${props.type === 'warning' ? 'bg-blue-500' : ''} text-white text-sm font-bold px-4 py-3 m-0`} 
            role="alert"
        >
        {!(props.type === "error") ? null :
            <img 
                src={props.type === 'error' ? ErrorSVG : null}
                className="fill-current w-4 h-4 mr-2"
            />
        }
        {!(props.type === "success") ? null :
            <img 
                src={props.type === 'success' ? SuccessSVG : null}
                className="fill-current w-4 h-4 mr-2"
            />
        }
        {!(props.type === "warning") ? null :
            <img 
                src={props.type === 'warning' ? WarningSVG : null}
                className="fill-current w-4 h-4 mr-2"
            />
        }
            <p dangerouslySetInnerHTML={{ __html: props.message }}></p>
        
        </div>
    )
}

export default Alert