import React, { useEffect } from "react"
import { Link, useNavigate } from "react-router-dom"

const Home = () => {
  const navigate = useNavigate()
  useEffect(() => {
    document.body.style = "background-image: url('/main-bg.jpeg');background-size: cover;background-position: center;"
    return () => {
      document.body.style = ""
    }
  })
  return (
    <div>
      <div
        className="heading text-white flex items-center justify-center relative top-20 flex-col text-4xl font-bold font-montserrat-subrayada"
      >
        <h1>WELCOME TO</h1>
        <h1>MENSTROMY</h1>
      </div>
      <div
        className="text-white flex items-center justify-center relative top-32 flex-col text-4xl font-semibold font-montserrat"
      >
        <h2>Lets Get Started</h2>
      </div>
      <div className="btns relative top-72">
        <div className="btn1 flex justify-center items-center mb-9">
          <button
            onClick={() => navigate('/signup')}
            className="bg-white text-black text-xl font-semibold py-4 px-12 rounded-full font-montserrat bg-gradient-to-br from-white to-gray-300"
          >
            I am new here
          </button>
        </div>
        <div className="btn2 flex justify-center items-center">
          <button
            onClick={() => navigate('/signin')}
            className="bg-transparent rounded-xl border-4 border-gray-100 text-xl font-semibold py-2 px-4 font-montserrat"
            style={{
              border: '2.5px solid',
              borderImage: 'linear-gradient(180deg, #ffffff, #e6c7c7)',
              borderImageSlice: 1
            }}
          >
            Sign In
          </button>
        </div>
      </div>
    </div>
  )
}

export default Home