import { BrowserRouter, Routes, Route, useLocation } from 'react-router-dom'
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import Home from './Pages/Home'
import Login from './Pages/Login'
import Signup from './Pages/Signup';
import ProtectedRoutes from './ProtectedRoutes';
import Details from './Pages/Details';
import Profile from './Pages/App/Profile';
import Dashboard from './Pages/App/Dashboard';
import Shop from './Pages/App/Shop';
import Product from './Pages/App/Product';
import Cart from './Pages/App/Cart';
import Checkout from './Pages/App/Checkout';
import Order from './Pages/App/Order';
import Orders from './Pages/App/Orders';
import Logbook from './Pages/App/Logbook';
import AppBase from './Components/Base/AppBase';

const App = () => {
  return(
    <BrowserRouter basename='/app'>
      <Routes>
        <Route path='/' element={<Home/>} />
        <Route path='/signin' element={<Login />} />
        <Route path='/signup' element={<Signup />} />

        {/* Protected Routes */}
        <Route element={<ProtectedRoutes />}>
          <Route path='/details' element={<Details />} /> 
          <Route path='/shop' element={<Shop />} /> 
          <Route path='/dashboard' element={<Dashboard />} />
          <Route path='/calendar' element={<Logbook />} />
          <Route path='/cart' element={<Cart />} />
          <Route path='/profile' element={<Profile />} />
          <Route path='/product/:productId' element={<Product />} />
          <Route path='/order/:orderId' element={<Order />} />
          <Route path='/orders' element={<Orders />} />
          <Route path='/checkout' element={<Checkout />} />


        </Route>
        <Route path="*" element={<AppBase><center><h1>No Page Found</h1></center></AppBase>}/>
      </Routes>
    </BrowserRouter>
  )
}


export default App