import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import Product from "../../Components/Shop/Product";
import { getProducts } from "../../Helpers/Shop";
import { ArrowPathIcon } from "@heroicons/react/24/solid";
import Alert from "../../Components/All/Alert";

const Shop = () => {

    const [products, setProducts] = useState(null)
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    useEffect(() => {
        
        getProducts(1)
            .then((data) =>  {
                if(data.success) {
                    setProducts(data)
                    setLoading(false)
                }else {
                    setError(data.message)
                }
            })
            .catch(err => setError(err))
    }, [])

    return (
        <AppBase>
            <div className="p-4">
                {!loading ? '' : <ArrowPathIcon width={30} height={30} className="animate-spin" /> }
                {!error ? '' : <Alert type="error" message="An Error Occured!" /> }

                {!products ?  'No Products Found' :
                    <Product 
                        products={products.products.docs}
                    />
                }
            </div>
        </AppBase>
    )
}

export default Shop