import React, { useEffect, useState } from "react";
import AppBase from "../../Components/Base/AppBase";
import { ArrowPathIcon } from "@heroicons/react/24/solid";
import Alert from "../../Components/All/Alert";
import { useNavigate, useParams } from "react-router-dom";
import { getOrder } from "../../Helpers/Checkout";
import CartProduct from "../../Components/Shop/CartProduct";


const Order = () => {

    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    const [order, setOrder] = useState(null)
    const navigate = useNavigate()
    const { orderId } = useParams()
    useEffect(() => {
        getOrder(orderId)
            .then((data) => {
                if (data.success && data.dbRes !== null) {
                    setOrder(data)
                    setLoading(false)
                } else {
                    setError(data.message)
                }
            })
            .catch(err => setError(err))

    }, [])


    return (
        <AppBase>
            { error ? <Alert /> :

            <div className="flex flex-col p-4">
                <div className="flex flex-col w-full items-start text-black">
                    <h2 className="text-2xl font-bold flex flex-row">
                        Order
                        <p className="mt-1 text-xl font-semi-bold text-gray-800">
                            #{order ? order.dbRes._id : ''}
                        </p>
                    </h2>
                    <hr class="border-t-2 border-gray-600 w-full" />
                    {!order ? '' :
                        <p className="text-lg">
                            <span className="font-semibold">{order.dbRes.address.firstName} {order.dbRes.address.lastName}</span><br />
                            <span className="text-wrap mr-3">{order.dbRes.address.houseNumber}, {order.dbRes.address.locality} ({order.dbRes.address.landmark}), {order.dbRes.address.street}, {order.dbRes.address.city}, {order.dbRes.address.state} - {order.dbRes.address.pincode}</span><br />
                            <span>Mobile Number: {order.dbRes.address.mobileNumber}</span><br />
                        </p>
                    }
                    <hr class="border-t-2 border-gray-600 w-full" />

                    <p className="text-lg font-semibold">
                        Payment Mode: {order ? order.dbRes.payment_type: ''}
                    </p>
                    <p className="text-lg font-semibold">
                        Order Status: {order  ? order.dbRes.order_status: ''}
                    </p>
                    <p className="text-lg font-semibold">
                        Payment Status: {!order ? "": order.dbRes.payment_status}
                    </p>
                    <hr class="border-t-2 border-gray-600 w-full" />
                    <p className="text-xl font-bold">
                        Total Amount: {order && order.dbRes.rzpOrder ? order.dbRes.rzpOrder.amount / 100 + '.00' : ''}
                    </p>
                </div>
                <hr class="border-t-2 border-gray-600 w-full" />
                <br />
                <h2 className="text-xl font-semibold">
                    Products:
                </h2>
                {!loading && order.dbRes ? <CartProduct checkout={true} removeItem={() => null} product={order.dbRes.products} /> : <Alert type="warning" message="No Items In Your Order!" />}

            </div>
            }
        </AppBase>
    )
}

export default Order