import { TrashIcon } from '@heroicons/react/24/solid'

const CartProduct = (props) => {
   
   
    return(
       
        props.product.map((item) => {
            return(
                <div key={item._id} className="flex flex-row shadow border-gray-100 border rounded-xl h-auto w-[350px] mb-2">
                    <div className="w-1/3">
                        <img 
                            src={item.productId.productImages.length > 0 ? item.productId.productImages[0] : 'Not Available'} 
                            className='h-full w-full'
                        />
                    </div>
                    <div className="w-2/3 flex flex-col p-2">
                        <h2 className="text-lg font-semibold mb-1">{item.productId.productName}</h2>
                        <p className="text-xl font-medium mb-1">{item.productId.productPrice}</p>
                        <p className="text-sm font-medium mb-1">Qty: {item.quantity}</p>
                        {props.checkout ? '':
                            <button onClick={() => props.removeItem(item._id)} className="w-[100px]">
                                <TrashIcon width={25} height={25} className='text-[#ff2f2f]' />
                            </button>
                        }
                    </div>
                </div>
            )
        })
    )
}

export default CartProduct